import mIcon from "../../components/m-icon/m-icon";
import {
	getIndex,
	donate,
	getChange_24h
} from "../../api/api.js";

export default {
	data() {
		return {
			propInfo: '',
			isHidden: this.$store.state.isHidden,
			changColor: '#EFEFF4',
			data1: '+0.91%',
			data2: '0.082',
			data3: '89,893,708',
			noBind: '暫未綁定',
			Bind: '已綁定',
			panVlaue: '198,878',
			donateValue: '10',
			rmb: '',
			red:false
		}
	},
	components: {
		mIcon
	},
	methods: {
		assetHidden() {
			if (this.isHidden == false) {
				this.changColor = '#9B9B9B';
				this.isHidden = true;
				uni.setStorageSync('ASSET_KEY', this.isHidden);
			} else {
				this.changColor = '#EFEFF4';
				this.isHidden = false;
				uni.setStorageSync('ASSET_KEY', this.isHidden);
			}
		},
		getMyIndex() {
			getIndex().then(data => {
				this.propInfo = data;
				// 限制小数点后为4位
				// console.log(this.propInfo)
				// console.log(this.data2)
				
				this.rmb = this.propInfo.balance * this.data2
			})
		},
		add() {
			this.donateValue++;
		},
		sub() {
			if (this.donateValue <= 0) {
				uni.showToast({
					icon: 'none',
					title: '不可以捐贈少於1個'
				});
			} else {
				this.donateValue--;
			}

		},
		donatePan() {
			if (this.donateValue <= 0) {
				uni.showToast({
					icon: 'none',
					title: '不可以捐贈少於1個'
				});
			} else {
				donate(this.donateValue).then(data => {
					this.getMyIndex();
				});
			}
		},
		getChang() {
			getChange_24h().then(data => {
				// console.log(data.data[0])
				this.data1 = data.data[0].change_24h
				// console.log(this.data1)
				if (this.data1 > 0) {
					this.data1 = "+" + this.data1
				} else {
					this.red = true
					this.data1 = this.data1
				}
				this.data3 = data.data[0].volume_24h
				this.data2 = data.data[0].current

			})

		},
		toIXX(){
			uni.navigateTo({
				url:"ixx"
			})
			
			}

	},
	onLoad() {
		this.getMyIndex();
		this.getChang()
	},
	onShow() {
		this.getMyIndex();
	}
}
